import { Scene } from "phaser";
import { GAME_CONSTANTS, GameKey } from "../config/GameConstants";

interface ChordData {
  chordType: string;
  basePoints: number;
  accumulatedPoints: number;
  startTime: number;
  isComplete: boolean;
}

export class ChordDetector {
  private scene: Scene;
  private currentChord: ChordData | null = null;
  private chordDefinitions: Map<string, { name: string; points: number }> = new Map();

  constructor(scene: Scene) {
    this.scene = scene;
    this.initializeChordDefinitions();
  }

  private initializeChordDefinitions(): void {
    // Triads (3-note chords) - 50 points each
    // C Major: S + F + H (C + E + G)
    this.chordDefinitions.set("S,F,H", { name: "C Major", points: GAME_CONSTANTS.CHORD_DETECTION.TRIAD_POINTS });
    // D Minor: D + G + J (D + F + A) 
    this.chordDefinitions.set("D,G,J", { name: "D Minor", points: GAME_CONSTANTS.CHORD_DETECTION.TRIAD_POINTS });
    // E Minor: F + H + K (E + G + B)
    this.chordDefinitions.set("F,H,K", { name: "E Minor", points: GAME_CONSTANTS.CHORD_DETECTION.TRIAD_POINTS });
    // F Major: G + J + S (F + A + C)
    this.chordDefinitions.set("G,J,S", { name: "F Major", points: GAME_CONSTANTS.CHORD_DETECTION.TRIAD_POINTS });
    // G Major: H + K + D (G + B + D)
    this.chordDefinitions.set("H,K,D", { name: "G Major", points: GAME_CONSTANTS.CHORD_DETECTION.TRIAD_POINTS });
    // A Minor: J + S + F (A + C + E)
    this.chordDefinitions.set("J,S,F", { name: "A Minor", points: GAME_CONSTANTS.CHORD_DETECTION.TRIAD_POINTS });
    // B Diminished: K + D + G (B + D + F)
    this.chordDefinitions.set("K,D,G", { name: "B Diminished", points: GAME_CONSTANTS.CHORD_DETECTION.TRIAD_POINTS });

    // Seventh Chords (4-note chords) - 100 points each
    // C Major 7: S + F + H + K (C + E + G + B)
    this.chordDefinitions.set("S,F,H,K", { name: "C Major 7", points: GAME_CONSTANTS.CHORD_DETECTION.SEVENTH_POINTS });
    // D Minor 7: D + G + J + S (D + F + A + C)
    this.chordDefinitions.set("D,G,J,S", { name: "D Minor 7", points: GAME_CONSTANTS.CHORD_DETECTION.SEVENTH_POINTS });
    // G Dominant 7: H + K + D + G (G + B + D + F)
    this.chordDefinitions.set("H,K,D,G", { name: "G Dominant 7", points: GAME_CONSTANTS.CHORD_DETECTION.SEVENTH_POINTS });

    // Power Chords (2-note chords) - 25 points each
    // C5: S + H (C + G)
    this.chordDefinitions.set("S,H", { name: "C5", points: GAME_CONSTANTS.CHORD_DETECTION.POWER_CHORD_POINTS });
    // D5: D + J (D + A)
    this.chordDefinitions.set("D,J", { name: "D5", points: GAME_CONSTANTS.CHORD_DETECTION.POWER_CHORD_POINTS });
    // F5: G + S (F + C)
    this.chordDefinitions.set("G,S", { name: "F5", points: GAME_CONSTANTS.CHORD_DETECTION.POWER_CHORD_POINTS });
    // G5: H + D (G + D)
    this.chordDefinitions.set("H,D", { name: "G5", points: GAME_CONSTANTS.CHORD_DETECTION.POWER_CHORD_POINTS });
  }

  update(activeKeys: Set<string>): { points: number; chordName: string | null } {
    const keyString = Array.from(activeKeys).sort().join(",");
    
    // Debug: Log the key combination being checked
    if (activeKeys.size > 1) {
      console.log("Keys pressed:", keyString);
    }
    
    const chordDef = this.chordDefinitions.get(keyString);

    if (chordDef) {
      // New chord detected or same chord continuing
      if (!this.currentChord || this.currentChord.chordType !== chordDef.name) {
        this.currentChord = {
          chordType: chordDef.name,
          basePoints: chordDef.points,
          accumulatedPoints: 0,
          startTime: this.scene.time.now,
          isComplete: false,
        };
      }

      // Calculate points earned this frame (not total accumulated)
      const elapsed = this.scene.time.now - this.currentChord.startTime;
      const progress = Math.min(elapsed / GAME_CONSTANTS.CHORD_DETECTION.ACCUMULATION_TIME, 1);
      const newAccumulatedPoints = Math.floor(this.currentChord.basePoints * progress);
      const pointsThisFrame = newAccumulatedPoints - this.currentChord.accumulatedPoints;
      
      this.currentChord.accumulatedPoints = newAccumulatedPoints;

      // Check if chord is complete
      if (progress >= 1 && !this.currentChord.isComplete) {
        this.currentChord.isComplete = true;
      }

      return { points: pointsThisFrame, chordName: this.currentChord.chordType };
    } else {
      // No valid chord detected
      this.currentChord = null;
      return { points: 0, chordName: null };
    }
  }

  getCurrentChord(): ChordData | null {
    return this.currentChord;
  }
}
